      </div>

      <form>
        <label>
          <span>Tổng số tài khoản:</span>
          <input :value="totalAcc" type="number" disabled/>
        </label>
        <label>
          <span>Tổng số ngọc 4 sao</span>
          <input :value="star4" type="number" disabled/>
        </label>
        <label>
          <span>Tổng số ngọc 5 sao</span>
          <input :value="star5" type="number" disabled/>
        </label>
