    </div>
  </div>
</template>

<script>
import axios from "axios";
import async from "async";
import config from '../config/default'
const host = config[process.env.NODE_ENV].host

export default {
  title () {
    return this.title
  },
  name: "Account",
  props: ["title"],
  data: function() {
    return {
      totalAcc: 0,
      star4: 0,
      star5: 0,
      account: null,
      header: [
        "STT",
        "Tài khoản",
        "Ngọc 4 sao",
        "Ngọc 5 sao",
        "Ngọc",
        "CSB",
        "TĐLT",
        "Cập nhật"
      ],
      keys: ["name", "star4", "star5", "gem", "csb", "autoTime", "updatedAt"],
      table: {
        data: [],
        header: "row",
        border: true,
        stripe: true,
        showCheck: true,
        enableSearch: true,
        pagination: true,
        pageSize: 10,
        pageSizes: [10, 20, 50],
        edit: {
          column: []
        }
      },
      change: {}
    };
  },
  created() {
    axios({
      method: "post",
      url: host + "/get-account"
    }).then(res => {
      console.log(res);
      this.account = res.data.content
      
    });
  },
  methods: {
    onCellChange(row, col, data) {
      this.change[row] = Object.assign({}, this.account[row - 1]);
      this.change[row][this.keys[col - 1]] = data;
    },
  },
  watch: {
    account: function() {
      let data = (this.account || []).map((c, i) => {
        return [
          i + 1,
          c.username,
          c.star4,
          c.star5,
          c.gem,
          c.csb,
          c.autoTime,
          c.updatedAt
        ];
      });
      this.totalAcc = (this.account || []).length;
      this.star4 = (this.account || []).map(_acc => _acc ? _acc.star4 : 0).reduce((a,b) => a + b) || 0
      this.star5 = (this.account || []).map(_acc => _acc ? _acc.star5 : 0).reduce((a,b) => a + b) || 0
      this.table.data = [this.header, ...data];
    }
  }
};
</script>