import Vue from 'vue'
import VueRouter from 'vue-router'
import { Account, Customer, Login } from '../components/index'
import store from '../store'

Vue.use(VueRouter)

const routes = [
  {
    path: '/tai-khoan',
    name: 'Account',
    component: Account,
    props: {
      title: "Tài khoản"
    },
    meta: {
      requireAuth: true
    }
  }, 
  {
    path: '/default',
    name: 'Customer',
    component: Customer,
    props: {
      title: "Default"
    },
    meta: {
      requireAuth: true
    }
  }, 
  {
    path: '/dang-nhap',
    name: 'Login',
    component: Login, 
    props: {
      title: "Đăng nhập"
    }
  }, 
  {
    path: '/',
    name: 'Login',
    component: Login, 
    props: {
      title: "Đăng nhập"
    }, 
    beforeEnter(to, from, next) {
      if(store.state.login) {
        return next({path: '/tai-khoan'})
      }
      next()
    }
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  if(store.state.login) {
    return next();
  }
  if(to.meta.requireAuth ) {
    return next({path: '/dang-nhap'})
  }
  next()
})
export default router
